<template>
	<div id="takeout-ordersubmit">
		<public-header title="订单提交"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell>
					<div slot="title">提交订单页面默认收货地址</div>
					<iswitch :value="order.use_default_accept_address" condition-open="1" condition-close="2" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.use_default_accept_address"></iswitch>
				</van-cell>
				<van-cell>
					<div slot="title">提交订单时需确认收货信息</div>
					<iswitch :value="order.audit_accept_address" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.audit_accept_address"></iswitch>
				</van-cell>
				<van-cell>
					<div slot="title">防客户拖定位降配送费<br><span class="font-12 c-gray">仅适用于按距离收取配送费的计费模式</span></div>
					<iswitch :value="order.check_member_drag_address" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.check_member_drag_address"></iswitch>
				</van-cell>
			</van-cell-group>
			<div class="padding-15">
				<van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				order: {
					use_default_accept_address: '1',
					audit_accept_address: '1',
					check_member_drag_address: '1'
				}
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'order',
					url: 'plateform/config/takeout/order'
				});
			},

			jsToggleSwitch(res){
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			},

			onSubmit(){
				var that = this;
				setData({
					vue: this,
					url: 'plateform/config/takeout/order',
					data: that.order
				});
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	
</style>


